.AccountLine{
  display: block;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}


.App-header {
  background-color: #66C39E;
  height: 3em;
  padding: 1em;
  color: black;
  padding-left: 10em;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.Counter{
  text-align: center;
  margin: 1em;
  /* float:left; */
}
.Footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #66C39E;
  color: white;
  text-align: center;
  margin-top: 2em;
}

h4{
display: inline-block;
}
.LoginForm{
  text-align: right;
  padding-right: 5em;
}

.LoginInput{
  display: inline;
  border-radius: 2px;
  border-color: black;
  border-width: 2px;
  margin: 0.5em;
  height: 2em;

}

.LoginButton{
  background-color: black;
  color: white;
  height: 2.2em;
  border-style: none;
  border-radius: 2px;
}
.logo {
  height: 3em;
  float: left;
}
.map{
  max-width: 40%;
  width: 35%;
  height: auto;
  margin: 2em;

  float: left;
  margin-left: 8em;
  /* padding: 2em; */
}
.Middle{
  float: right;
  margin-right: 2em;
  width: 40%
}
.NewAccountForm {
  padding-bottom: 3em;
}
.NewAccountButton{
  background-color: #66C39E;
  color: white;
  height: 2.2em;
  padding-left: 9.2em;
  padding-right: 9.2em;
  border-style: none;
  border-radius: 5px;
  font-size: 1em;
}
